.lds-heart {
  width: 100px;
  height: 100px;
  transform-origin: 40px 40px;
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
  position: relative;
  transform: rotate(45deg);
}

.lds-heart div {
  width: 32px;
  height: 32px;
  background: var(--accent-text-color);
  animation: lds-heart 1.2s cubic-bezier(.215, .61, .355, 1) infinite;
  position: absolute;
  top: 32px;
  left: 32px;
}

.lds-heart div:after, .lds-heart div:before {
  content: " ";
  width: 32px;
  height: 32px;
  background: var(--accent-text-color);
  display: block;
  position: absolute;
}

.lds-heart div:before {
  border-radius: 50% 0 0 50%;
  left: -24px;
}

.lds-heart div:after {
  border-radius: 50% 50% 0 0;
  top: -24px;
}

@keyframes lds-heart {
  0% {
    transform: scale(.95);
  }

  5% {
    transform: scale(1.1);
  }

  39% {
    transform: scale(.85);
  }

  45% {
    transform: scale(1);
  }

  60% {
    transform: scale(.95);
  }

  100% {
    transform: scale(.9);
  }
}

